 <template id="movie">
    <h1 @click="change()" ref="title">movie{{msg}}{{this.$store.state.count}}</h1>
</template>
<script>
    export default {
        data(){
            return{
                msg:0,
                n:0
            }
        },
        // watch
        // (){
            // 
        // }
        watch:{
            msg(){
                console.log('')
            },
            n(){
                console.log('n')
            }
        },
        methods:{
            change(){
                // this.$store.state.count++
                // this.$store.commit('increment')
                this.$store.commit('add',100)
                this.msg++
            }
        },
        // vue
        beforeCreate(){
            // 
            console.log('beforeCreate........................')
           
            console.log(this.msg)
             // vue this.$refs.ref
             console.log(this.$refs.title)
        },
        created(){
            // 
            console.log('created........................');
            console.log(this.msg)
             // vue this.$refs.ref
             console.log(this.$refs.title)
        },
        beforeMount(){
            // 
             console.log('beforeMount........................');
            console.log(this.msg)
             // vue this.$refs.ref
             console.log(this.$refs.title)
        },
        mounted(){
            // ,
             console.log('mounted........................');
            console.log(this.msg)
             // vue this.$refs.ref
             console.log(this.$refs.title)
        },
        beforeUpdate(){
             // 
             console.log('beforeUpdate........................');
            console.log(this.msg)
             // vue this.$refs.ref
             console.log(this.$refs.title)
        },
        updated(){
              // 
             console.log('updated........................');
            console.log(this.msg)
             // vue this.$refs.ref
             console.log(this.$refs.title)
        },
        beforeDestroy(){
            // 
            // .$destroy()

        },
        destroyed(){
            // 

        }
    }
</script>


