<template >
    <main>
        <h1 @click="add()">{{this.$store.state.count}}</h1>
        <ul>
            <li v-for="(item,index) in list">
                <router-link :to="'/details/'+item.id">{{item.con}}</router-link>
            </li>
        </ul>

        <main class="zsh">
            <mt-swipe :auto="4000">
                <mt-swipe-item>
                    <img src="http://img63.ddimg.cn/upload_img/00785/ts0824_0907/1242X366_0820-1597993817.jpg" alt="">
                </mt-swipe-item>
                <mt-swipe-item>
                    <img src="http://img63.ddimg.cn/upload_img/00785/ts0824_0907/1242X366_0820-1597993817.jpg" alt="">
                </mt-swipe-item>
                <mt-swipe-item>
                    <img src="http://img63.ddimg.cn/upload_img/00785/ts0824_0907/1242X366_0820-1597993817.jpg" alt="">
                </mt-swipe-item>
                 <mt-swipe-item>
                    <img src="http://img63.ddimg.cn/upload_img/00785/ts0824_0907/1242X366_0820-1597993817.jpg" alt="">
                </mt-swipe-item>
                 <mt-swipe-item>
                    <img src="http://img63.ddimg.cn/upload_img/00785/ts0824_0907/1242X366_0820-1597993817.jpg" alt="">
                </mt-swipe-item>
                 <mt-swipe-item>
                    <img src="http://img63.ddimg.cn/upload_img/00785/ts0824_0907/1242X366_0820-1597993817.jpg" alt="">
                </mt-swipe-item>

            </mt-swipe>
        </main>

        <mt-range
  v-model="rangeValue"
  :min="10"
  :max="90"
  :step="1"
  :bar-height="5">
   <div slot="start">0</div>
  <div slot="end">100</div>
</mt-range>
{{rangeValue}}

<mt-header title="">
  <router-link to="/" slot="left">
    <mt-button icon="back"></mt-button>
  </router-link>
  <mt-button icon="more" slot="right"></mt-button>
</mt-header>

<mt-navbar v-model="selected">
  <mt-tab-item id="1"></mt-tab-item>
  <mt-tab-item id="2"></mt-tab-item>
  <mt-tab-item id="3"></mt-tab-item>
</mt-navbar>
{{selected}}
<!-- tab-container -->
<mt-tab-container v-model="selected">
  <mt-tab-container-item id="1">
    <mt-cell v-for="n in 10" :title="11111" />
  </mt-tab-container-item>
  <mt-tab-container-item id="2">
    <mt-cell v-for="n in 4" :title="' ' + n" />
  </mt-tab-container-item>
  <mt-tab-container-item id="3">
    <mt-cell v-for="n in 6" :title="' ' + n" />
  </mt-tab-container-item>
</mt-tab-container>

<mt-search
  v-model="value"
  cancel-text=""
  placeholder="">
</mt-search>

<mt-field label="" placeholder="" v-model="username"></mt-field>
<mt-field label="" placeholder="" type="email" v-model="email"></mt-field>
<mt-field label="" placeholder="" type="password" v-model="password"></mt-field>
<mt-field label="" placeholder="" type="tel" v-model="phone"></mt-field>
<mt-field label="" placeholder="" type="url" v-model="website"></mt-field>
<mt-field label="" placeholder="" type="number" v-model="number"></mt-field>
<mt-field label="" placeholder="" type="date" v-model="birthday"></mt-field>
<mt-field label="" placeholder="" type="textarea" rows="4" v-modal="introduction"></mt-field>
    </main>
</template>

<script>
import { Toast } from 'mint-ui';
    export default{
        methods:{
            add(){
                Toast({
        message: '',
        position: 'top',
        duration: 1000
        });
            }
        },
    data(){
        return {
            selected:"1",
            rangeValue:20,
            list:[
                {
                    con:'',
                    id:'game'
                },
                {
                    con:'',
                    id:'tv'
                }
            ]
        }
    }
}
</script>

<style >

    .zsh img{
        width:100%;
    }
    .zsh .mint-swipe-items-wrap{
        /* padding-bottom */
        padding-bottom:29.46%;
    }
    .zsh .mint-swipe-indicator{
        background:red;
        opacity: 0.9;
    }
    .zsh .is-active{
        background:blue
    }
</style>

