// vuexvue
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    // state data  this.$store.state.
    // 
  state: {
    count: 0
  },
//   mutationsmethods this.$store.commit('',k,k)k
  mutations: {
    increment (state) {
      state.count++
    },
    add(state,n){
        state.count=state.count+n;
    }
  }
})


